.card__list {
  display: flex;
  flex-wrap: wrap;
  flex-basis: calc(100% / 3);
  justify-content: space-between;
}

.card__item {
  margin: 0 auto;
  margin-bottom: 101px;
}

.card__link {
  text-decoration: none;
}

.card__image {
  position: relative;
}

.card__caption {
  position: absolute;
  left: 50%;
  bottom: -0.5px;
  transform: translateX(-50%);
  font-size: 24px;
  line-height: 29px;
  font-weight: 500;
  letter-spacing: -0.415px;
  width: 215px;
  text-align: center;
  padding: 16px 0 17px 0;
  background-color: $basic-white;
  color: $dark;
}
